














body {
  background: url("/static/images/1-1.jpg") no-repeat;
}
.top-banner {
  position: absolute;
  right: 1rem;
  width: 1.44rem;
  height: 3rem;
  background: url("/static/images/WechatIMG2.png") no-repeat;
  background-size: 100% 100%;
}
.top-banner .top-banner-tip {
    position: absolute;
    bottom: .45rem;
    left: 0.25rem;
    width: 1rem;
    height: 0.3rem;
    text-align: center;
    color: #a57c50;
    font-weight: 600;
    font-size: .2rem;
}
.home-back, .item-list-wrapper {
  position: absolute;
  width: 6rem;
  height: 5.2rem;
  top: 2.2rem;
  left: .6rem;
}
.button {
  position: absolute;
  width: 2rem;
  height: 1rem;
  top: 7.5rem;
  left: 2.5rem;
  background-size: 100% 100%;
  cursor: pointer;
}
.start {
  background-image: url("/static/images/1-4.png");
}
.next {
  background-image: url("/static/images/2-2.png");
}
.submit {
  background-image: url("/static/images/3-1.png");
}
.item-list-wrapper {
  background-image: url("/static/images/2-1.png");
  background-size: 100% 100%;
}
.item-list-wrapper .item-list {
    position: absolute;
    top: 1rem;
    left: 1.3rem;
    font-size: 0.3rem;
    color: #fff;
}
.item-list-wrapper .item-list ul {
      margin-top: 0.2rem;
}
.item-list-wrapper .item-list ul .item {
        margin-top: 0.2rem;
        font-size: 0.25rem;
        cursor: pointer;
}
.item-list-wrapper .item-list ul .item .item-pre {
          display: inline-block;
          margin-right: 0.2rem;
          border: 1px solid #fff;
          border-radius: 50%;
          vertical-align: middle;
          text-align: center;
          line-height: 0.35rem;
          width: 0.35rem;
}
.hasChoosed {
  background-color: #ffd400;
  color: #575757;
  border-color: #ffd400;
}

body {
  background-image: url("/static/images/4-1.jpg");
  padding-top: 1rem;
}
.score-wrapper {
  width: 4.3rem;
  height: 4rem;
  background-image: url("/static/images/4-2.png");
  background-size: 100% 100%;
  margin: 0 auto;
}
.score-wrapper .score {
    width: 4.3rem;
    text-align: center;
    position: absolute;
    top: 3rem;
    left: 2rem;
    font-size: .8rem;
    font-weight: bold;
}
.score-wrapper .score .score-num, .score-wrapper .score .fenshu {
      font-family: Tahoma,Helvetica,Arial;
      color: #a51d31;
}
.score-wrapper .result-tip {
    position: absolute;
    color: #3e2415;
    font-size: 0.3rem;
    top: 4.1rem;
    left: 1.9rem;
    text-align: center;
    width: 4rem;
}
.share-btn {
  text-align: center;
  margin-top: .5rem;
}
.share-btn img {
    width: 2.68rem;
    height: 1.06rem;
}
.qrcode {
  text-align: center;
  font-size: 0.25rem;
  margin-top: 0.3rem;
}
.qrcode img {
    margin-top: 0.2rem;
    height: 2.36rem;
    width: 2.36rem;
}
.share-cover {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("/static/images/5-1.png");
  background-size: 100% 100%;
  opacity: .92;
}
.share-cover img {
    width: 5.34rem;
    height: 4.9rem;
    position: fixed;
    top: 0.2rem;
    left: 50%;
    margin-left: -2.67rem;
}
